import React, { useState, useRef, useEffect } from 'react';
import Hammer from 'hammerjs';
import { Document, Page, pdfjs } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

const PDFViewer = ({ filePath }) => {
    const [scale, setScale] = useState(1);  // PDF 
    const [numPages, setNumPages] = useState(null);
    const containerRef = useRef(null);  // 

    const onLoadSuccess = ({ numPages }) => {
        setNumPages(numPages);
    };

    const handlePinch = (event) => {
        // 
        const scaleFactor = event.scale;
        const centerX = event.center.x;
        const centerY = event.center.y;

        // 
        setScale(prevScale => Math.max(0.5, prevScale * scaleFactor));

        // 
        const container = containerRef.current;
        const containerRect = container.getBoundingClientRect();

        // 
        const X = (centerX - containerRect.left) * (scaleFactor - 1);
        const Y = (centerY - containerRect.top) * (scaleFactor - 1);

        // 
        container.style.transform = `scale(${scale * scaleFactor}) translate(${X}px, ${Y}px)`;
    };

    useEffect(() => {
        const hammer = new Hammer.Manager(containerRef.current);

        // 
        hammer.add(new Hammer.Pinch());
        hammer.on('pinch', handlePinch);

        return () => {
            hammer.off('pinch', handlePinch);
        };
    }, [scale]);

    return (
        <div
            ref={containerRef}
            style={{
                // overflow: 'hidden',
                maxWidth: '100%',
                transformOrigin: '0 0', // 
            }}
        >
            <Document
                file={filePath} // PDF 
                onLoadSuccess={onLoadSuccess}
                renderMode="svg"
            >
                {Array.from(new Array(numPages), (el, index) => (
                    <Page key={`page_${index + 1}`} pageNumber={index + 1} scale={scale} />
                ))}
            </Document>
        </div>
    );
};

export default PDFViewer;
